@import 'extend/reset';

body {
  font-size: 18px;
  line-height: 28px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  position: relative;
}


.contain {
  max-width: 1245px;
  padding: 0 10px;
  display: block;
  margin: 0 auto;
  overflow: hidden;
}

.bg-header {
  background-repeat: no-repeat;
  position: absolute;
  height: 921px;
  display: block;
  margin: 0 auto;
  left: 0;
  right: 0;
  opacity: 0.2;
  width: 100%;
  background-position: center top;
  z-index: -1;
  background-size: cover;
}

.bold {
  font-weight: bold;
}

#show_menu {
  background: none;
  border: none;
  outline: none;
  display: none;

  &:hover {
    outline: none;
    border: none;
  }

}


#nav-main {
  padding: 14px 0 50px;
  position: fixed;
  z-index: 100;
  background-color: #FFF;
  width: 100%;
  max-width: 100% !important;

  img {
    width: auto !important;
    border-width: 0px;
    border: 0px;
    padding-left: 142px;
  }

  ul {
    margin: 6px 0;
    padding-left: 0;
    margin: 0 auto !important;
    max-width: 1032px;
    width: 100%;
  }

  li {
    display: inline-block;
    list-style: none;
    color: #fff;
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
      padding: 4px 12px !important;
      font-style: italic;
      color: #0c224a;
      border: 1px solid #8dc441;
      border-radius: 8px;
    }

  }

  a {
    font-weight: 600;
    text-decoration: none;
    color: #0c224a;
    font-size: 18px;
    transition: color 300ms ease;

    &:hover {
      color: #75cb00;
    }

  }

}





// new home2

.home-link-txt {
  font-size: 14px;
  margin-top: 50px;
}

.btn-border-link {
  border: solid 3px #8bc341;
  outline: none;
  color: #8bc341;
  text-decoration: none;
  background-color: #fff;
  font-size: 14px;
  padding: 8px 30px;
  font-weight: 700;
  display: inline-block;
  margin-top: 30px;
}

.mac {
  background-image: url("https://cdn2.hubspot.net/hubfs/3875530/assets/home/mac.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: inline-block;
  background-size: auto;
  top: 463px;
  height: 573px;
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  display: block;
}

.mac-sub-cont {
  max-width: 540px;
  display: block;
  margin: 0 auto;
  padding-top: 8%;
}

.prod {
  font-size: 10px;
  display: block;
  vertical-align: middle;
  margin: 0 -16px;

  .prod-item {
    display: inline-block;
    max-width: 100px;
    vertical-align: middle;
    text-align: center;
    margin: 12px 15px;

    p {
      text-align: center;
      font-size: 10px;
      display: block;
      width: 100%;
      line-height: 1;
      margin-top: 14px;
      font-weight: 600;
    }

  }

  h3 {
    display: inline-block;
    vertical-align: middle;
    width: 192px;
    margin: 10px 15px;
    text-align: left;
  }

}

.prod img {
  width: 82px;
  height: 82px;
  vertical-align: middle;
}





#productos {
  margin-top: -110px;
  position: relative;
  z-index: 100;
  padding: 0 40px 110px;
}

.home-links {
  display: block;
  max-width: 720px;
  margin: 0 auto;
  font-size: 0;

  .home-link-item {
    vertical-align: top;
    font-size: 18px;
    width: 50%;
    display: inline-block;
    vertical-align: top;

    &:first-child {
      background-image: url("https://cdn2.hubspot.net/hubfs/3875530/assets/visite_blog.png");
      background-repeat: no-repeat;
      padding-left: 60px;
      border-right: 1px solid #ddd;
      color: #7c8590;
      font-size: 14px;

      p {
        margin: 0;
        line-height: 17px;
      }

    }

    &:last-child {
      padding-left: 59px;
    }

    &:nth-child(2) {
      background-position: left 32px top;
      background-image: url("https://cdn2.hubspot.net/hubfs/3875530/assets/guias_apre.png");
      background-repeat: no-repeat;
      color: #7c8590;
    }

    &:last-child {
      padding-left: 99px;
    }

  }

}